body {
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  background-color: rgb(3, 3, 55);
  color:rgb(3, 3, 55)
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}


.App {
  display: grid;
  grid-template-areas:
    'header header header header header'
    'left main main main right'
    'footer footer footer footer footer';
  grid-template-columns: repeat(5, minmax(0, 1fr));
  gap: 10px;
  padding: 10px;
}

.App > div {
  background-color: rgba(255, 255, 255, 0.7);
  padding: 20px;
}

.Header {
  grid-area: header;
  display: grid;
  grid-template-areas: 'headerIcon menubar menubar menubar headerExtra';
  grid-template-columns: repeat(5, minmax(0, 1fr));
  gap: 10px;
  padding: 0px;
}

.Header > div {
  padding: 20px;
}
.Menubar{
  grid-area: menubar;
}
.HeaderIcon {
  grid-area: headerIcon;
}
.HeaderExtra {
  grid-area: headerExtra;
}

.Right {
  grid-area: right;
}

.Left {
  grid-area: left;
}

.MainContent {
  grid-area: main;
}
